import {Component} from 'react'
import Axios from 'axios';

import VicePresident from "./components/VicePresident"
import President from './components/President';
import Thanks from './components/Thanks'
import Scaning from './components/Scaning'



class App extends Component {

  state= {k: 0,result: ""}
  
  submitForm = (voting)=>{
    
    const {result} = this.state
    Axios.post("http://localhost:8000/president",{vote: voting,result: result,})
    .then((data)=>{
      this.setState({k: 2})
      alert(`${result} Successfully casted Vote for the President of SAC`)
    })
    .catch((e)=>{
      console.log("Error in Casting the vote",e)
    })
  }


submitForm2=(voting)=>{

    const {result} = this.state
    Axios.post("http://localhost:8000/vice",{vote: voting,result: result,})
    .then((data)=>{
      this.setState({k: 3})
      alert(`${result} Successfully casted your Vote for Vice-president of SAC`)
    })
    .catch((e)=>{
      console.log("Error in Casting the vote",e)
    })
  }


  
  scannerFunction = (res) =>{
    
    Axios.post("http://localhost:8000/credentials",{result: res,})
    .then  ((obj)=>{
      console.log("scanner return :  ",obj.data,obj)
      if(obj.data ===  res){
        this.setState({result: obj.data})
        this.setState({k:1})
      }
      else{
        this.setState({k: -1})
      }
    })
    .catch((e)=>{
      console.log(e)
    })
  }

  render(){
    const {k,result} = this.state

    if(k=== -1){
     return <Scaning scannerFunction={this.scannerFunction} para={true}/>
    }
     else if(result !== "" && k===0){
       return <Scaning scannerFunction={this.scannerFunction} para={false}/>
     }
     else if(k===0){
       return <Scaning scannerFunction={this.scannerFunction} para = {false}/>
     }
     else if(k===1){
       return  <President  submitForm={this.submitForm}/>
     }
     else if(k===2){
       return <VicePresident submitForm2={this.submitForm2}/>
     }
     else if(k===3){
       return <Thanks/>
     }
  }
}


export default App;