import React, { useState, useEffect } from 'react';
import Quagga from 'quagga';
import './index.css'


const  Scaning=(props) =>{

  let [result, setResult] = useState("");

  const {scannerFunction} = props

  useEffect(() => {
    Quagga.init({
      inputStream: {
        type : 'LiveStream',
        constraints: {
          width: 640,
          height: 480,
          facingMode: "environment"
        },
        target: document.querySelector('#scanner')
      },
      decoder: {
        readers : ['code_128_reader']
      }
    }, function(err) {
        if (err) {
            console.log(err);
            return
        }
        Quagga.start();
    });

    Quagga.onProcessed(function(result) {
        var drawingCtx = Quagga.canvas.ctx.overlay,
            drawingCanvas = Quagga.canvas.dom.overlay;

        if (result) {
            if (result.boxes) {
                drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute("width")), parseInt(drawingCanvas.getAttribute("height")));
                result.boxes.filter(function (box) {
                    return box !== result.box;
                }).forEach(function (box) {
                    Quagga.ImageDebug.drawPath(box, {x: 0, y: 1}, drawingCtx, {color: "green", lineWidth: 2});
                });
            }

            if (result.box) {
                Quagga.ImageDebug.drawPath(result.box, {x: 0, y: 1}, drawingCtx, {color: "#00F", lineWidth: 2});
            }

            if (result.codeResult && result.codeResult.code) {
                Quagga.ImageDebug.drawPath(result.line, {x: 'x', y: 'y'}, drawingCtx, {color: 'red', lineWidth: 3});
            }
        }
    }
    );

    Quagga.onDetected(function(result) {
        console.log("Barcode detected and processed : [" + result.codeResult.code + "]", result);
        setResult(result.codeResult.code);
        Quagga.stop();
    });
    
    return () => {
        Quagga.stop();
    }
  },[]);
  
  function scanning_function(){

    if(result !== ""){
      scannerFunction(result)
    }

    return(
            <div className="bg-container">
              <h1 class="heading">Scan for Voting</h1>
              <div id="scanner" className="scanner " ></div>
              
             <p className="head">{result ? `Barcode value: ${result}` :''}</p>
             {props.para === true ? <p className="error">Please refresh and Try again... </p> : ""}
            </div>
          ) 
  }

  return(
  scanning_function()
  )
}

export default Scaning